import Vue from 'vue'
import Router from 'vue-router'
// 登录
const Login = resolve => require(['../pages/login'], resolve)
// 登陆之后的首页
const IndexHtml = resolve => require(['../pages/indexHtml'], resolve)
// 切换页面
const SwitchContent = resolve => require(['../pages/switchContent'], resolve)
// system configuration页面
const System = resolve => require(['../pages/system'], resolve)
// reports 页面
const Reports = resolve => require(['../pages/reports'], resolve)
// administration 页面
const Administration = resolve => require(['../pages/administration'], resolve)
// about 页面
const About = resolve => require(['../pages/about'], resolve)
Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/pages/login',
      meta: { requireAuth: false },
      component: Login
    },
    {
      path: '/',
      meta: {
        requireAuth: true
      },
      component: IndexHtml,
      redirect: '/pages/switchContent', //重定向到第一个子路由，否则只渲染Layout组件
      children: [
        {
          path: '/pages/switchContent',
          meta: { requireAuth: true },
          component: SwitchContent
        },
        {
          path: '/pages/system',
          meta: { requireAuth: true },
          component: System
        },
        {
          path: '/pages/reports',
          meta: { requireAuth: true },
          component: Reports
        },
        {
          path: '/pages/administration',
          meta: { requireAuth: true },
          component: Administration
        },
        {
          path: '/pages/about',
          meta: { requireAuth: true },
          component: About
        },
      ]
    }
  ]
})
//当一个导航触发时，全局的 before 钩子按照创建顺序调用。钩子是异步解析执行，此时导航在所有钩子 resolve 完之前一直处于等待中。
router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) { // 判断该路由是否需要登录权限
    if (sessionStorage.getItem('access_token')) { //判断本地是否存在access_token
      if (window.myInterval) {
        window.clearInterval(window.myInterval);
      }
      if (window.myInterval1) {
        window.clearInterval(window.myInterval1);
      }

      next();
    } else {
      if (window.myInterval) {
        window.clearInterval(window.myInterval);
      }
      if (window.myInterval1) {
        window.clearInterval(window.myInterval1);
      }
      next({
        path: '/pages/login'
      })
    }
  }
  else {
    if (window.myInterval) {
      window.clearInterval(window.myInterval);
    }
    if (window.myInterval1) {
      window.clearInterval(window.myInterval1);
    }
    next();
  }
  /*如果本地 存在 token 则 不允许直接跳转到 登录页面*/
  if (to.fullPath == "/pages/login") {
    if (sessionStorage.getItem('access_token')) {
      next({
        path: from.fullPath
      });
    } else {
      next();
    }
  }
})
export default router


